<template>
    <div id="nav" class="nav" v-if="isShowHeader">
                <v-toolbar dark tabs >
                        <!-- <v-tab slot="extension" color="transparent" fixed-tabs slider-color="white"> -->
                    <v-toolbar-title @click="changeLoction('/')">
                    <img class="animated tada" src="/img/logo.png">
                    </v-toolbar-title>
                    <div class="main-nav">
                      <v-tabs color="transparent">
                            <v-tab @click="changeLoction('/')">
                                Home
                            </v-tab>
                            <v-menu v-if="loggedInUserId && isRegisteredUser" color="white" offset-y open-on-hover activator lazy class="v-tabs__div">
                                <a slot="activator" class="v-tabs__item">
                                    Bussiness
                                    <v-icon>arrow_drop_down</v-icon>
                                </a>
                                <v-list>
                                    <v-list-tile v-for="(item, index) in items" :key="index" @click="changeLoction(`/${loggedInUserId}/${item.route}`)">
                                        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                                    </v-list-tile>
                                </v-list>
                            </v-menu>
                      </v-tabs >
                    </div>
                    <div class="login">
                      <v-tabs slider-color="transparent" color="transparent" >
                        
                    <v-tab flat  v-if="(loggedInUserId && !isRegisteredUser) || !loggedInUserId" @click="changeLoction('/login')">
                        login
                    </v-tab>
                    <v-tab flat v-if="loggedInUserId && isRegisteredUser" @click="logout">
                        logout
                    </v-tab>
                    <v-tab v-if="loggedInUserId && isRegisteredUser">
                    <v-avatar @click="changeLoction(`/${loggedInUserId}/bussinessProfile`)">
                    <div class="profile"></div>
                    </v-avatar>
                    </v-tab>
                        <!-- </v-tabs> -->

                      </v-tabs>
                    </div>
                  </v-toolbar>
    </div>
</template>

<script>
import userService from "@/services/userService.js";
import {
  ACT_CHECK_USER_LOGIN,
  GETTER_IS_LOGIN,
  GETTER_USER_ID,
  GETTER_IS_REGISTER_USER,
  MUT_LOGOUT_USER
} from "@/store/userModule";
import {
  eventBus,
  EVENT_TOGGLE_HEADER_PAGE
} from "@/services/event-bus-service.js";

export default {
  data() {
    return {
      user: "",
      items: [
        { title: "Bussiness Calender", route: "bussinessCalender" },
        { title: "Bussiness Profile", route: "bussinessProfile" },
        { title: "Bussiness Page", route: "editPage" }
      ],
      // isRegisteredUser: this.$store.getters[GETTER_IS_REGISTER_USER],
      // loggedInUserId: this.$store.getters[GETTER_IS_LOGIN]
    };
  },

  created() {
    // eventBus.$on(EVENT_TOGGLE_HEADER_PAGE, _ => {
    //   this.isShowHeader = !this.isShowHeader;
    // });

    this.$store.dispatch({ type: ACT_CHECK_USER_LOGIN }).then(user => {
      this.user = user;
    });
  },
  methods: {
    changeLoction(url) {
      this.$router.push(url);
    },
    logout() {
      console.log('logging out...')
      this.$store.commit({ type: MUT_LOGOUT_USER })
      userService.logout()
      this.$router.push('/')
    }
  },
  computed: {
    loggedInUserId() {
      return this.$store.getters[GETTER_IS_LOGIN];
    },
    isShowHeader() {
      if (!this.loggedInUserId && this.$route.name === "publishPage") {
        return false;
      }
      return true;
    },
      isRegisteredUser() {
      return this.$store.getters[GETTER_IS_REGISTER_USER]
    }
  }
};
</script>

<style lang="scss" scoped>
.v-toolbar__title img {
  width: 150px;
  height: 8vh;
}
.v-toolbar__title {
  cursor: pointer !important;
}
.main-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.login {
  display: flex;
}

.login * {
  background-color: transparent;
}
.profile {
  border-radius: 50%;
  width: 50px;
  height: 100%;
  background-color: #42b983;
  background-image: url("data:image/png;base64,");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.v-toolbar__items {
  height: fit-content;
}
a.v-list__tile,
a .v-list__tile--link {
  color: #2c3e50 !important;
}
.nav .application,
.nav .theme--light {
  background-color: rgba(0, 0, 0, 0.5) !important;
}
.v-toolbar,
.v-toolbar--extended,
.theme--darkr {
  background-color: rgba(0, 0, 0, 0.5) !important;
}
.application--wrap {
  min-height: unset;
}
.v-tabs__item {
  color: white;
}
.nav {
  z-index: 5;
  position: fixed;
  width: 100%;
  margin: 0;
  padding: 0;
  // text-decoration: ;
  a {
    font-weight: bold;
    color: white;
    font-size: 0.9em;
    &.router-link-exact-active {
      color: #42b983;
    }
  }
  a:hover {
    color: #e23c52;
  }
}
@media only screen and (min-width: 700px) {

}
</style>

